.page-header {
  width: 100%;
  color: #ffffff;
  background-color: #131314;
  background-image: url("../img/intro.jpg");
  background-size: 1440px 825px;
  background-repeat: no-repeat;
  background-position: 50%; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/intro@2x.jpg"); } }

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 220px;
  width: 880px;
  margin: 0 auto; }

.page-header__intro {
  width: 395px; }

.page-header__form {
  width: 327px; }

.main-nav {
  margin-bottom: 255px;
  width: 100%; }

.site-list {
  display: flex;
  padding: 21px 25px 9px 22px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.site-list__item {
  flex-grow: 1;
  padding-right: 10px; }
  .site-list__item:last-of-type {
    flex-grow: 0;
    padding-right: 0; }

.site-list__link {
  font-weight: 300;
  opacity: 0.7; }

.intro__item--title {
  margin: 0;
  margin-top: 31px;
  font-weight: 700;
  font-size: 46px;
  letter-spacing: -0.23px;
  line-height: 64px; }

.intro__item--text {
  margin: 0;
  margin-top: 24px;
  font-weight: 300;
  font-size: 20px;
  letter-spacing: -0.1px;
  opacity: 0.8; }

.form {
  padding: 4px 0 20px; }

.form__text {
  margin: 0;
  text-align: center;
  font-size: 17px;
  line-height: 28px; }

.form__item {
  width: 100%;
  margin-top: 20px; }

.form__input {
  padding: 14px 10px 10px 30px;
  font-size: 15px;
  line-height: 24px;
  color: #1f1f1f;
  border-radius: 4px;
  border: solid 1px #c9ccd4; }

.form__input::placeholder {
  color: #1f1f1f;
  opacity: 0.5; }

.form__button {
  display: flex;
  justify-content: center;
  padding: 15px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  background-color: #0ad9c6;
  border-width: 0;
  border-radius: 6px; }
